.feed_container {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 60px;
  height: 912px;
}

.link {
  text-decoration: none;
  color: #fff;
}

.orders_scroll_container {
  display: flex;
  flex-direction: column;
  gap: 16px;
  overflow-y: scroll;
}

.orders_scroll_container::-webkit-scrollbar {
  width: 12px;
}
.orders_scroll_container::-webkit-scrollbar-track {
  background: #2f2f37;
}
.orders_scroll_container::-webkit-scrollbar-thumb {
  background: #8585ad;
  border: 3px solid #2f2f37;
  border-radius: 6px;
}
